html, body, #root, .App {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  background-color: #f4f4f4;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;

  /* Prevent tap to zoom */
  touch-action: manipulation;
  -webkit-user-select: none;
  user-select: none;
}

.flipbook {
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.5);
  background-color: #fff;
}

.page {
  width: 100%;
  height: 100%;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.zoom-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  touch-action: pinch-zoom;
  -webkit-overflow-scrolling: touch;
}

/* Prevent accidental tap zoom/focus */
.flip-img {
  width: 100%;
  height: auto;
  object-fit: contain;
  border-radius: 8px;
  pointer-events: none;
  user-select: none;
  touch-action: none;
}

/* Arrows */
.nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  background: rgba(255, 255, 255, 0.6);
  border: none;
  font-size: 2rem;
  padding: 10px 15px;
  color: black;
  cursor: pointer;
  border-radius: 50%;
}

.prev {
  left: 20px;
}

.next {
  right: 20px;
}
